<template>
  <div class="container pool-container launch-container">
    <el-breadcrumb separator="/" class="crumbs-nav">
      <el-breadcrumb-item :to="{ path: '/crowd' }">众筹建池</el-breadcrumb-item>
      <el-breadcrumb-item>发起众筹建池</el-breadcrumb-item>
    </el-breadcrumb>
    <el-container>
      <el-main>
        <div class="pool-left">
          <div class="pool-left-cont">
            <h2>深度图预览</h2>
            <div class="left-tab-cont">
              <h5>发起众筹建池，帮助您和社区共建代币流动性，仅需以下几步操作</h5>
              <ul>
                <li>
                  <span>1</span>
                  <h4>邀请社区</h4>
                  <p>设置众筹建池相关参数，即可一键发起。发起后可以邀请社区成员关注、参与众筹。</p>
                </li>
                <li>
                  <span>2</span>
                  <h4>参与众筹</h4>
                  <p>社区通过参与众筹建池可以公平获得代币份额，当超募发生时会按照投入的资金比例分配份额，多余资金会退回。</p>
                </li>
                <li>
                  <span>3</span>
                  <h4>领取份额</h4>
                  <p>参与者根据资金比例获得代币份额，项目方的代币和众筹获得资金将会创建一个流动性资金池。项目方可以在流动性保护期之后取出池子里的资金。</p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </el-main>
      <el-aside width="450px">
        <div class="pool-right">
          <h3>设置参数</h3>
          <div class="right-tab-line">
            <h5>01 设置众筹类型</h5>
            <template>
              <el-tabs v-model="rightTab" type="card" @tab-click="tabClick">
                <el-tab-pane label="定价众筹" name="publicpond">
                  <h5>02 设置众筹代币</h5>
                  <div class="form-line1">
                    <div class="form-line1-balance"><span>余额：{{0}}</span><el-button size="mini">Max</el-button></div>
                    <div class="form-line1-input">
                      <div class="line-input-val">选择<i class="el-icon-caret-bottom"></i></div>
                      <el-input v-model="publicsellnum1" placeholder="0.0"></el-input>
                    </div>
                    <div class="form-line1-cont2">
                      <span>众筹比例(%)</span>
                      <el-tooltip placement="top" offset= '-60'>
                        <div slot="content">众筹售卖的代币比例上限。</div>
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                      <el-input v-model="privatepoint2" placeholder="0-50"></el-input>
                    </div>
                    <div class="form-line1-input form-line2-input">
                      <div class="line-input-val">
                        <span>众筹代币数</span>
                        <el-tooltip placement="top" offset= '-60'>
                          <div slot="content">社区可以购买到的代币总数。</div>
                          <i class="el-icon-question"></i>
                        </el-tooltip>
                      </div>
                      <div class="line-input-cont">{{'-'}} SH</div>
                    </div>
                    <div class="form-line1-cont2 form-line1-cont3">
                      <span>众筹销售价格(%)</span>
                      <el-tooltip placement="top" offset= '-60'>
                        <div slot="content">众筹售卖的代币比例上限。</div>
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                      <el-input v-model="privatepoint2" placeholder="0.0000000"></el-input>
                      <div class="line-input-val">选择<i class="el-icon-caret-bottom"></i></div>
                    </div>
                    <div class="form-line1-input form-line2-input">
                      <div class="line-input-val">
                        <span>目标筹集资金</span>
                      </div>
                      <div class="line-input-cont">{{'-'}} WHT</div>
                    </div>
                  </div>
                  <h5>03 设置众筹参数</h5>
                  <div class="form-line1-cont2 form-line1-cont3 form-line1-cont4">
                    <span>众筹开始时间</span>
                    <el-tooltip placement="top" offset= '-60'>
                      <div slot="content">众筹时间开始后，用户才能参与。</div>
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                    <el-date-picker
                      v-model="starttime"
                      type="datetime"
                      placeholder="选择开始日期时间" :picker-options="pickerStart" align = 'right'>
                    </el-date-picker>
                  </div>
                  <div class="form-line1-cont2 form-line1-cont3 form-line1-cont4">
                    <span>众筹结束时间</span>
                    <el-tooltip placement="top" offset= '-60'>
                      <div slot="content">用户在众筹建池结束后可获得代币。</div>
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                    <el-date-picker
                      v-model="endtime"
                      type="datetime"
                      placeholder="选择结束日期时间" :picker-options="pickerEnd" align = 'right'>
                    </el-date-picker>
                  </div>
                  <div class="form-line1-cont2 form-line1-cont3">
                    <div class="line1-cont3-title">
                      <span>流动性保护天数</span>
                      <el-tooltip placement="top" offset= '-60'>
                        <div slot="content">众筹流动性保护期结束后，可提取全部资金。</div>
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                    </div>
                    <el-tabs class="protect-days" v-model="protectdays" @tab-click="protectClick">
                      <el-tab-pane label="30天" name="days30"></el-tab-pane>
                      <el-tab-pane label="90天" name="days90"></el-tab-pane>
                      <el-tab-pane label="180天" name="days180"></el-tab-pane>
                    </el-tabs>
                  </div>
                </el-tab-pane>
                <el-tab-pane label="升价众筹" name="privatepond">
                  <h5>02 设置众筹代币</h5>
                  <div class="form-line1">
                    <div class="form-line1-balance"><span>余额：{{0}}</span><el-button size="mini">Max</el-button></div>
                    <div class="form-line1-input">
                      <div class="line-input-val">选择<i class="el-icon-caret-bottom"></i></div>
                      <el-input v-model="publicsellnum1" placeholder="0.0"></el-input>
                    </div>
                    <div class="form-line1-cont2 form-line1-cont3">
                      <span>初始销售价格(%)</span>
                      <el-tooltip placement="top" offset= '-60'>
                        <div slot="content">会以此价格拍卖代币。</div>
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                      <el-input v-model="privatepoint2" placeholder="0.0000000"></el-input>
                      <div class="line-input-val">选择<i class="el-icon-caret-bottom"></i></div>
                    </div>
