                    <span>冷静期结束时间</span>
                    <el-tooltip placement="top" offset= '-60'>
                      <div slot="content">冷静结束时间。</div>
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                    <el-date-picker
                      v-model="chilltime"
                      type="datetime"
                      placeholder="选择冷静期结束日期时间" :picker-options="pickerChill" align = 'right'>
