      chilltime:'',
      rightTab:'publicpond',
      // 公共池
      publicbuynum1:'',
      publicsellnum1:'',
      publicbuynum2:'',
      publicbuynum3:'',
      publicsellnum2:'',
      publicsellnum3:'',
      publicratenum1:'0.3',
      publicratenum2:'0.3',
      publicratenum3:'0.3',
      publicminprice2:'1',
      publicminprice3:'1',
      publicpoint2:'0.3',
      publicpoint3:'0.3',
      // 私有池
      privatesellnum1:'',
      privatebuynum1:'',
      privateratenum1:'',
      privatesellnum2:'',
      privateratenum2:'',
      privateminprice2:'',
      privatepoint2:'',

      protectdays:'days30',
      otherdays:'date30',
      pickerStart: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        }
      }, 
      pickerEnd: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        }
      },
      pickerChill: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        }
      } 
    }
  },
  computed: { 
  },
  watch: {},
  created () {},
  mounted () {},
  methods: {
    tabClick(tab) {

    },
    protectClick(tab){

    }
  }
}
</script>
